import React, { Fragment } from 'react';
import '../styles/main.css';
import Navbar from './Navbar/Navbar';
import Carousel from './Carousel/Carousel';
import Blog from './Blog/Blog';
import Features from './Features/Features';
import Products from './Products/Products';
import Footer from './Footer/Footer';
import FlashSale from './FlashSale/FlashSale';

export default function Main() {
    var appear = [
        {
            scrollY: 300,
            className: 'flash-sale-container',
            added: false,
            index: 0,
        }, {
            scrollY: 900,
            className: 'products-container',
            added: false,
            index: 0,
        }, {
            scrollY: 1600,
            className: 'products-container',
            added: false,
            index: 1,
        },  {
            scrollY: 2200,
            className: 'products-container',
            added: false,
            index: 2,
        },  {
            scrollY: 10000,
            className: 'products-container',
        },
    ];
    return (
        <Fragment>
            <Navbar />
            <div className="content">
                <div class="board">
                    <Carousel />
                    <Blog />
                </div>
                <Features />
                <FlashSale />
                <Products />
            </div>
            <Footer />
            {
            window.addEventListener("scroll", function (event) {
                const zoom = window.devicePixelRatio / 1.25;
                console.log(this.scrollY, this.screen.height, zoom, this.pageYOffset);
                if (zoom < 0.25) {
                    zoom = 0.1;
                }
                for (let i = 0; i < appear.length; i++) {
                    if (i < appear.length - 1) {
                        if (this.scrollY >= appear[i].scrollY * zoom && this.scrollY < appear[i+1].scrollY * zoom) {
                            if (appear[i].added == false) {
                                console.log(this.scrollY, appear[i].className);
                                document.getElementsByClassName('hide')[0].classList.add(appear[i].className);
                                document.getElementsByClassName(appear[i].className)[appear[i].index].classList.remove('hide');
                                appear[i].added = true;
                            }
                        }
                    }
                }
            })
        }
        </Fragment>
    );
} 