import React, { Component, Fragment } from 'react';
import '../../styles/navbar.css';
import {items} from './Variables';

class ListItem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            class: 'nav-item',
        }
        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);
    }
    
    handleMouseEnter() {
        this.setState({
            class: this.state.class + ' is-focused'
        })
    }

    handleMouseLeave() {
        this.setState({
            class: 'nav-item'
        })
    }

    render() {
        const result = (child) => {
            if (child != undefined) {
                return (
                    <div className="nav-dropdown">
                        <div className="nav-dropdown-container">
                            {
                                child.map(item => (
                        
                                    <div className="nav-dropdown-column">
                                        {
                                            item.map(chunk => (
                                                <a className={chunk.class} href={chunk.href}>{chunk.text}</a>
                                            ))
                                        }
                                    </div>
                                ))
                            }
                        </div>
                    </div>
                );
            }
            return <Fragment></Fragment>
        }
        return (
            <li onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.class}>
                <a href={this.props.href}>{this.props.text}</a>
                { result(this.props.child) }
            </li>
        );
    }
}

class YourCart extends Component {
    constructor(props) {
        super(props);
        this.state = {
            class: 'your-cart'
        }
    }

    render() {
        return (
            <div class="your-cart">
                    <img src="assets/icons/cart.png" />
                    <div>GIỎ HÀNG</div>
                    <span id="cart-number">0</span>
            </div>
        );
    }
}

class SearchBox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            class: 'search',
            clicked: false
        }
        this.handleOnBlur = this.handleOnBlur.bind(this);
        this.handleOnFocus = this.handleOnFocus.bind(this);
    }

    handleOnFocus() {
        this.setState({
            class: this.state.class + ' is-focused',
            clicked: true
        })
    }

    handleOnBlur() {
        this.setState({
            class: 'search',
            clicked: false
        })    
    }

    render() {
        return (
            <div className={this.state.class} id="search">
                    <input onFocus={this.handleOnFocus} onBlur={this.handleOnBlur} placeholder="SEARCH"/>
                    <img src="assets/icons/search.png" />
            </div>
        );
    }
}

class Navbar extends Component {
    render() {
        return (
            <header>
                <nav class="navbar">
                <img src="assets/icons/logo.png" id="logo"/>
                <div class="brand">
                    <h1>ABE SHOES</h1>
                    <p>Đồng hành cùng bạn</p>
                </div>
                <ul>
                    { items.map(item => <ListItem {...item} />) }
                </ul>
                <YourCart />
                <SearchBox />
            </nav>
            </header>
        );
    }
};

export default Navbar;